












































import Vue from 'vue';
export default Vue.extend({
  name: 'HelloWorld',
  data(){
    return {
      tagTitle: '黑龙江11选5',    //默认彩种
      len: 5,                    //默认长度
      nowTime: '',
      issue: null,
      ball: [],
      lot: [
        {
          name: '11选5',
          len: 5,
          list: [
            {name: '黑龙江11选5'},
            {name: '吉林11选5'},
            {name: '辽宁11选5'},
            {name: '广东11选5'},
            {name: '山东11选5'},
            {name: '江西11选5'},
            {name: '河北11选5'},
            {name: '天津11选5'},
            {name: '北京11选5'},
            {name: '四川11选5'},
            {name: '陕西11选5'},
            {name: '河南11选5'},
            {name: '青海11选5'},
          ]
        },
        {
          name: '快乐十分',
          len: 8,
          list: [
            {name: '黑龙江快乐十分'},
            {name: '广东快乐十分'},
            {name: '重庆快乐十分'},
            {name: '天津快乐十分'},
            {name: '陕西快乐十分'},
            {name: '云南快乐十分'},
            {name: '广西快乐十分'},
          ]
        },
        {
          name: '快三',
          len: 3,
          list: [
            {name: '吉林快3'},
            {name: '江苏快3'},
            {name: '河北快3'},
            {name: '内蒙快3'},
            {name: '安徽快3'},
            {name: '北京快3'},
            {name: '河南快3'},
            {name: '宁夏快3'},
          ]
        }
      ],
      tagInput: '',
    }
  },
  methods: {
    entry(name: string, len: number): void {
      this.tagTitle = name;
      this.len = len;
    },
    getNow(){
      const formatNumber: any = (n: string)=> {
        n = n.toString();
        return n[1] ? n : '0' + n
      };
      var date = new Date();
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hour = date.getHours();
      const minute = date.getMinutes();
      const second = date.getSeconds();
      this.nowTime = [year, month, day].map(formatNumber).join('-') + 'T' + [hour, minute, second].map(formatNumber).join(':');
    },
    add(){
      var bool = this.ball.some(function(val){
        return isNaN(val);
      });
      if(!this.issue || this.ball.length !== this.len || bool){
        this.$message({
          message: '请输入正确的期号及开奖号码',
          type: 'warning',
          duration: 1500,
        });
      }else{
        this.$message({
          message: '开奖号码录入成功',
          type: 'success',
          duration: 1500,
        });
        this.issue = null;
        this.ball = [];
        this.getNow();
      }
    },
  },
  mounted: function(){
    this.$nextTick(function(){
      this.getNow();
    })
  }
});
